* {
  margin: 0;
}

.App {
  display:flex;
  flex-direction: column;
}

.App_top {
  display:flex;
  justify-content: space-evenly;
}
.App_left_header{
  padding: 1px;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  justify-content: space-between;
}

.App_left_boxes{
  display:flex;
  justify-content: space-evenly;
}

.App_dropdown{
background-color: #fff;
       min-width: 10px;
       max-width: 245px;
       width:245px;
}
.App_header_text{
color: #e50914;
font-size: 24px;
font-weight: 600;
font-family: 'Times New Roman', Times, serif;
padding:10px
}

.App_left{
  padding:10px 10px;
  flex:.5;
  min-width: 820px;
}
.App_right{
  padding:10px 10px;
  flex:.5;
  display: flex;
}

.App_right_chart{
  margin-left: 10px;
  display: flex;
  flex-direction: column;
}
 @media (max-width:850px) {
  .App_top {
    flex-direction: column;
  }
}
