.table{
   height: 100%;
   width: 100%;
   max-width: 850px;
}

.table01{
    margin-top: 10px;
    overflow: scroll;
    color: #6a5d5d;
    background-color: white;
    height: 490px;
}

.table01 tr {
    display: flex;
    justify-content: space-between;
  }
  
  .table01 td {
    padding: 0.5rem;
    border: none;
    font-size: 14px;
    font-weight: 500;
  }
  
  .table01 tr:nth-of-type(odd) {
    background-color: #f3f2f8;
  }

  .table01::-webkit-scrollbar {
    display: none;
  }
  .thead{
    padding: 0.5rem;
    border: none;
    font-size: 16px;
    font-weight: 700;
  }

  
  .info-flag img {
    width: 200px;
    border-radius: 5px;
  }
  
  .info-name {
    font-size: 15px;
    font-weight: bold;
    color: #555;
  }
  
  .info-container {
    width: 200px;
  }
  
  .info-flag {
    height: 80px;
    width: 150px;
    background-size: cover;
    border-radius: 8px;
  }
  
  .info-confirmed,
  .info-recovered,
  .info-deaths {
    font-size: 16px;
    font-weight: 500;
    font-family:Georgia, 'Times New Roman', Times, serif;
    margin-top: 5px;
  }